@tailwind components;

@layer components {
  .skills__block {
    @apply min-w-full sm:min-w-[24ch] text-xl xs:text-2xl sm:text-3xl;
  }

  .skills__technologies {
    @apply w-full grid gap-4 justify-items-center transition-all;
    grid-template-columns: repeat(auto-fit, minmax(7rem, 1fr));
  }
}
